.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  width: auto;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header .is-active {
  color: #00acdc;
  position: relative;
}

.header .is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.nav {
  align-items: center;
  display: block;
}

.nav__list {
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 15px;
  padding: 22.5px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: inline;
}

.nav__item {
  margin: 0 10px;
  display: flex;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__list a:hover {
  color: #00acdc;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  text-transform: transform .3s;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  width: 100%;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 16px;
  font-weight: 400;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-values {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 164px;
  height: 38px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card .reviews {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  font-weight: 400;
  display: flex;
}

.card .reviews__text {
  color: #060b35;
  text-align: right;
  white-space: nowrap;
  font-size: 10px;
  line-height: 14px;
}

* {
  margin: 0;
}

body {
  margin: 0;
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 500;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

